<template>
  <div id="app" :class="{
    'dark-theme': $store.state.isDarkTheme,
    'modal-opened': isModalOpen
  }">

    <router-view/>

    <Demo v-show="checkIsModalOpen" />
    <SignUp v-show="checkIsModalOpen" />
    <PasswordReset v-show="checkIsModalOpen" />
    <SignIn v-show="checkIsModalOpen" />
    <Fair v-show="checkIsModalOpen" />

    <div class="fix-m-panel">
      <div class="container">
        <FixHint />

        <div class="fix-support-pan">
          <button class="fix-support-pan__button" type="button">
            <svg class="icon">
              <use xlink:href="./assets/images/sprite.svg#circle-supp"></use>
            </svg>
            <span>Онлайн служба поддержки</span>
          </button>
        </div>
      </div>
    </div>

    <!-- game wheel modals -->
    <WheelHow2Play v-show="checkIsModalOpen" />
    <WheelHistory v-show="checkIsModalOpen" />

    <!-- game pvp modals -->
    <PvpHistory v-show="checkIsModalOpen" />
    <PvpHow2Play v-show="checkIsModalOpen" />

    <!-- game crash modals -->
    <CrashHow2Play v-show="checkIsModalOpen" />

    <!-- game dice modals -->
    <DiceHow2Play v-show="checkIsModalOpen" />

    <!-- game coinflip modals -->
    <CoinflipHow2Play v-show="checkIsModalOpen" />

    <!-- news madals -->
    <News2 v-show="checkIsModalOpen" />
    <News1 v-show="checkIsModalOpen" />
    <News3 v-show="checkIsModalOpen" />
  </div>
</template>

<script>
import { addLockScrollCLass, removeLockScrollCLass } from './helpers/changeLockScrollClass.js'
import SignIn from './components/modals/SignIn.vue'
import SignUp from './components/modals/SignUp.vue'
import PasswordReset from './components/modals/PasswordReset.vue'
import Demo from './components/modals/Demo.vue'
import Fair from './components/modals/Fair.vue'
import FixHint from './components/FixHint.vue'
import WheelHow2Play from './components/modals/WheelHow2Play.vue'
import WheelHistory from './components/modals/WheelHistory.vue'
import PvpHistory from './components/modals/PvpHistory.vue'
import PvpHow2Play from './components/modals/PvpHow2Play.vue'
import CrashHow2Play from './components/modals/CrashHow2Play.vue'
import DiceHow2Play from './components/modals/DiceHow2Play.vue'
import CoinflipHow2Play from './components/modals/CoinflipHow2Play.vue'
import News1 from './components/modals/News1.vue'
import News2 from './components/modals/News2.vue'
import News3 from './components/modals/News3.vue'

export default {
  name: 'App',
  components: {
    SignIn,
    SignUp,
    PasswordReset,
    Demo,
    Fair,
    WheelHow2Play,
    WheelHistory,
    PvpHistory,
    PvpHow2Play,
    CrashHow2Play,
    DiceHow2Play,
    CoinflipHow2Play,
    FixHint,
    News1,
    News2,
    News3
  },
  data () {
    return {
      isModalOpen: false
    }
  },
  computed: {
    checkIsModalOpen () {
      if (this.isModalOpen) {
        addLockScrollCLass()
      } else {
        removeLockScrollCLass()
      }

      return this.isModalOpen
    }
  }
}
</script>
