import Vue from 'vue'
import VueRouter from 'vue-router'

import Main from '../screens/Main.vue'
import Preview from '../screens/Preview.vue'
import Layout from '../components/Layout.vue'
import Account from '../screens/Account.vue'
import Profile from '../screens/Profile.vue'
import RefSys from '../screens/RefSys.vue'
import Reviews from '../screens/Reviews.vue'
import Help from '../screens/Help.vue'
import Wallet from '../screens/Wallet.vue'
import WalletBalance from '../screens/WalletBalance.vue'
import WalletWithdraw from '../screens/WalletWithdraw.vue'
import WalletHistory from '../screens/WalletHistory.vue'
import WalletEmpty from '../components/WalletEmpty.vue'
import FreeCoins from '../screens/FreeCoins.vue'
import GameWheel from '../screens/GameWheel.vue'
import GamePvp from '../screens/GamePvp.vue'
import GameCrash from '../screens/GameCrash.vue'
import GameDice from '../screens/GameDice.vue'
import GameCoinflip from '../screens/GameCoinflip.vue'
import Terms from '../screens/Terms.vue'
import Policy from '../screens/Policy.vue'

// delete for production
import ModalsForCust from '../screens/ModalsForCust.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/main',
    name: 'Main',
    component: Main
  },
  {
    path: '/layout',
    name: 'Layout',
    component: Layout,
    children: [
      { // delete for production
        path: '/modals',
        name: 'ModalsForCust',
        component: ModalsForCust
      },
      {
        path: '',
        name: 'Account',
        component: Account
      },
      {
        path: '/terms',
        name: 'Terms',
        component: Terms
      },
      {
        path: '/policy',
        name: 'Policy',
        component: Policy
      },
      {
        path: '/profile',
        name: 'Profile',
        component: Profile
      },
      {
        path: '/refSys',
        name: 'RefSys',
        component: RefSys
      },
      {
        path: '/reviews',
        name: 'Reviews',
        component: Reviews
      },
      {
        path: '/help',
        name: 'Help',
        component: Help
      },
      {
        path: '/wallet',
        name: 'Wallet',
        component: Wallet,
        children: [
          {
            path: '',
            name: 'WalletEmpty',
            component: WalletEmpty,
          },
          {
            path: '/wallet/balance',
            name: 'WalletBalance',
            component: WalletBalance,
          },
          {
            path: '/wallet/withdraw',
            name: 'WalletWithdraw',
            component: WalletWithdraw,
          },
          {
            path: '/wallet/history',
            name: 'WalletHistory',
            component: WalletHistory,
          }
        ]
      },
      {
        path: '/coins',
        name: 'Coins',
        component: FreeCoins
      },
      {
        path: '/game-wheel',
        name: 'GameWheel',
        component: GameWheel
      },
      {
        path: '/game-pvp',
        name: 'GamePvp',
        component: GamePvp
      },
      {
        path: '/game-crash',
        name: 'GameCrash',
        component: GameCrash
      },
      {
        path: '/game-dice',
        name: 'GameDice',
        component: GameDice
      },
      {
        path: '/game-coinflip',
        name: 'GameCoinflip',
        component: GameCoinflip
      },
    ]
  },
  {
    path: '/*',
    name: 'Preview',
    component: Preview
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
