<script>
import { Pie } from "vue-chartjs";

export default {
  name: 'GamePvPChart',
  extends: Pie,
  mounted() {
    this.renderChart(
      {
        labels: [],
        datasets: [
          {
            backgroundColor: ["#008aff", "#41bec3", "#ff7c00"],
            data: [40, 20, 10],
            borderWidth: 4, // Это отвечает за отступы между элементами
            borderColor: '#f9f8f6'
          },
        ],
      },
      {
        responsive: true,
        maintainAspectRatio: false,
        cutoutPercentage: 92, // Это отвечает за ширину полосок (больше значение = меньше полоска)
        events: [], // Отключает тултипы и hover
      }
    );
  },
};
</script>