/* eslint-disable global-require */
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isDarkTheme: false,
    menu: {
      isMobileOpened: false,
    }
  },
  getters: {
    
  },
  mutations: {
    ToggleMobileMenuState(state) {
      state.menu.isMobileOpened = !state.menu.isMobileOpened;
    },
    ToggleDarkThemeState(state) {
      state.isDarkTheme = !state.isDarkTheme;
    }
  },
  actions: {

  },
  modules: {

  },
});
